<template>
    <div class="upload">
    </div>
</template>

<script>
    export default {
        name: 'vue-upload',
        props: {
            accept: {
                type: Object,
                default: null,
            },
            // 
            url: {
                type: String,
                default: '',
            },
            //  100
            fileNumLimit: {
                type: Number,
                default: 100,
            },
            //  2M
            fileSingleSizeLimit: {
                type: Number,
                default: 2048000,
            },
            // tokenkey
            formData: {
                type: Object,
                default: null
            },
            // formDatakey
            keyGenerator: {
                type: Function,
                default(file) {
                    const currentTime = new Date().getTime();
                    const key = `${currentTime}.${file.name}`;
                    return key;
                },
            },
            multiple: {
                type: Boolean,
                default: false,
            },
            // ID
            uploadButton: {
                type: String,
                default: '',
            },
        },
        data() {
            return {
                uploader: null
            };
        },
        mounted() {
            this.initWebUpload();
        },
        methods: {
            initWebUpload() {
                this.uploader = WebUploader.create({
                    auto: true, // 
                    swf: '/static/lib/webuploader/Uploader.swf',  // swf
                    server: 'http://192.168.0.222:8081/fileSystem/uploadBigFile',  // 
                    pick: {
                        id: this.uploadButton,     // 
                        multiple: this.multiple,   //  false
                        label: '',
                    },
                    accept: this.getAccept(this.accept),  // 
                    threads:1,
                    fileNumLimit: this.fileNumLimit, // 
                    //fileSingleSizeLimit: this.fileSingleSizeLimit, // 
                    formData: this.formData,  // 
                    chunked: true,          //
                    chunkSize: 20 * 1024 * 1024,    //
                    duplicate: true,  // 
                });
                // 
                this.uploader.on('fileQueued', (file) => {
                    this.$emit('fileChange', file);
                });
                this.uploader.on('uploadStart', (file) => {
                    // formData
                    //this.uploader.options.formData.key = this.keyGenerator(file);
                });
                // 
                this.uploader.on('uploadProgress', (file, percentage) => {
                    this.$emit('progress', file, percentage);
                });
                this.uploader.on('uploadSuccess', (file, response) => {
                    this.$emit('success', file, response);
                });
                this.uploader.on('uploadError', (file, reason) => {
                    console.error(reason);
                    this.$emit('uploadError', file, reason);
                });
                this.uploader.on('error', (type) => {
                    let errorMessage = '';
                    if (type === 'F_EXCEED_SIZE') {
                        errorMessage = `${this.fileSingleSizeLimit / (1024 * 1000)}M`;
                    } else if (type === 'Q_EXCEED_NUM_LIMIT') {
                        errorMessage = '';
                    } else {
                        errorMessage = `${type}`;
                    }
                    console.error(errorMessage);
                    this.$emit('error', errorMessage);
                });
                this.uploader.on('uploadComplete', (file, response) => {
                    this.$emit('complete', file, response);
                });
            },
            upload(file) {
                console.log(file)
                this.uploader.upload(file);
            },
            stop(file) {
                console.log(file)
                this.uploader.stop(file);
            },
            // 
            cancelFile(file) {
                console.log(file)
                this.uploader.cancelFile(file);
            },
            // 
            removeFile(file, bool) {
                this.uploader.removeFile(file, bool);
            },
            getAccept(accept) {
                switch (accept) {
                    case 'text':
                        return {
                            title: 'Texts',
                            exteensions: 'doc,docx,xls,xlsx,ppt,pptx,pdf,txt',
                            mimeTypes: '.doc,docx,.xls,.xlsx,.ppt,.pptx,.pdf,.txt'
                        };
                        break;
                    case 'video':
                        return {
                            title: 'Videos',
                            exteensions: 'mp4',
                            mimeTypes: '.mp4'
                        };
                        break;
                    case 'image':
                        return {
                            title: 'Images',
                            exteensions: 'gif,jpg,jpeg,bmp,png',
                            mimeTypes: '.gif,.jpg,.jpeg,.bmp,.png'
                        };
                        break;
                    default: return accept
                }
            },
        },
    };
</script>

<style lang="less">
    .webuploader-container {
        position: relative;
    }
    .webuploader-element-invisible {
        position: absolute !important;
        clip: rect(1px 1px 1px 1px); /* IE6, IE7 */
        clip: rect(1px,1px,1px,1px);
    }
    .webuploader-pick-disable {
        opacity: 0.6;
        pointer-events:none;
    }
</style>


