<template>
    <div class="receive">
        <search :data="searchData" :config="searchConfig" @search="handlerSearch"></search>
        <my-table :data="tableData" :config="tableConfig" @pageChange="pageChange" @receive="receive" ></my-table>
        <my-toast :config="toastConfig" @submit="submit" @cancel="toastConfig.dialogVisible=false;$refs.ruleForm.resetFields()"> 
            <el-form :model="form" :rules="rules" ref="ruleForm" label-width="110px" style="margin:0 20px;">
                <el-row>
                  <el-col :span="12">
                        <el-form-item label="" >
                            <span>{{form.insideOrder}}</span>
                        </el-form-item>
                  </el-col>
                  <el-col :span="12">
                      <el-form-item label="" >
                            <span>{{form.prodName}}</span>
                        </el-form-item>
                  </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                      <el-form-item label="" >
                            <span>{{form.amount}}</span>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                            <el-form-item label="" >
                                <span>{{form.crafworkName}}</span>
                            </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                  <el-col :span="12">
                        <el-form-item label="" prop="planDate">
                            <el-date-picker
                                v-model="form.planDate"
                                type="datetime"
                                value-format="yyyy-MM-dd HH:mm:ss"
                                 size="small"
                                 style="width:100%"
                                placeholder="">
                            </el-date-picker>
                        </el-form-item>
                  </el-col>
                  <el-col :span="12">
                      <el-form-item label="" prop="planHours">
                            <el-input-number :min="0" v-model="form.planHours" :controls="false" size="small"></el-input-number>
                        </el-form-item>
                  </el-col>
                </el-row>
                <el-form-item label="" prop="actDate">
                    <el-date-picker
                        v-model="form.actDate"
                        type="datetime"
                        value-format="yyyy-MM-dd HH:mm:ss"
                            size="small"
                            style="width:100%"
                        placeholder="">
                    </el-date-picker>
                </el-form-item>
            </el-form>
        </my-toast>
    </div>
</template>
<script>
import {mapState} from 'vuex'
import rule from '../../assets/js/rule.js'
export default {
    data(){
        return{
            //
            userList:[],
            selectData:[],
            rules:{
                price:rule.require('number',''),
                doEmpNo:rule.require('string',''),
                planDate:rule.require('string',''),
                planHours:rule.require('number',''),
                actDate:rule.require('string',''),
            },
            searchData:{},
            searchConfig: [
                [
                    {
                        label: "",
                        field: "insideOrder",
                        type: "input",
                        placeholder: ""
                    },
                    {
                        label: "",
                        field: "prodName",
                        type: "input",
                        placeholder: ""
                    },
                    {
                        label: "",
                        field: "status",
                        type: "select",
                        placeholder: "",
                        childrens:[{name:'',value:'0'},{name:'',value:'1'},{name:'',value:'2'},{name:'',value:'3'}]
                    },
                ]
            ],
            tableData:[],
            tableConfig:{
                isHigh: false,
                isSelection: true,
                align: "center",
                isPage: true,
                pageNo: 1,
                pageSize: 20,
                total: 0,
                colConfig: [
                    {
                        fixed: false,
                        field: "insideOrder",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "prodName",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "crafworkName",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "price",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "doEmpName",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "amount",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "planDate",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "actDate",
                        width: "120",
                        isSort: false,
                        isResize: true,
                        label: "",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "getTime",
                        isSort: false,
                        isResize: true,
                        label: "",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "crafworkStandHours",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "planHours",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "statusName",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "",
                        align: "center",
                        type: "button",
                        btnData:[
                            {
                                name:'',
                                method:'receive'
                            }
                        ]
                    }
                ]
            },
            toastConfig:{
                title:'',//
                dialogVisible:false,//
                width:'560px',//
                btnData:[{
                    isShow:true,
                    disabled:false,
                    name:'',//
                    method:'cancel',//
                },{
                    disabled:false,
                    isShow:true,
                    type:'primary',
                    name:'',//
                    method:'submit',//
                }]
            },
            form:{

            },
        }
    },
    computed:{
        ...mapState(['ports'])
    },
    methods:{
        getList(){
            this.$http.post(
                this.ports+'/taskPondMes/listForDrawTask',
                {
                    pageSize:this.tableConfig.pageSize,
                    pageNo:this.tableConfig.pageNo,
                    ...this.searchData
                },
                data=>{
                    this.tableData=data.items;
                    this.tableConfig.total=data.numRows;
                },
                this
            )
        },
        handlerSearch(){
            this.tableConfig.pageNo=1
            this.tableConfig.pageSize=20
            this.getList()
        },
        submit(){
            this.$refs['ruleForm'].validate((valid) => {
                if (valid) {
                    this.$http.post_noqs(
                        this.ports+'/taskPondMes/drawTask',
                        this.form,
                        data=>{
                            this.$refs.ruleForm.resetFields()
                            this.dialogVisible=true
                            this.$message({
                                type: 'success',
                                message:''
                            })
                            this.toastConfig.dialogVisible=false
                            this.getList()
                        },
                        this
                    )
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        receive(val){
            if(val.status!=0){
                return this.$message.error('')
            }
            this.form=Object.assign({},val)
             this.toastConfig.dialogVisible=true
            // this.$http.post(
            //     this.ports+'/taskPondMes/getById',
            //     {id:val.id},
            //     data=>{
            //         console.log(data)
            //         this.userList=data
                   
            //     },
            //     this
            // )
            
        },
        pageChange(){
            console.log('111')
            this.getList()
        }
    },
    mounted(){
        this.getList()
    }
}
</script>
<style lang="less">
    .receive{
       padding:20px;
       background:#fff;
        .my-list{
            padding:0 20px;
            .template{
                display: flex;
                .text{
                    flex: 1;
                }
                .operate{
                    width: 100px;
                    padding-left: 30px;
                    font-size: 14px;
                }
            }
        }
    }
</style>




