<template>
  <div>
    <page :pageData="pageData"></page>
  </div>
</template>
<script>
import {mapState} from 'vuex'
  export default {
    data() {
      return {
        pageData: {
          pocHeaderData: [],
          sidebarData: [{
            visible: true,
            title: 'MES',
            chlid: [
              {
                name:'',
                router:'wk',
                chlid:[
                  {
                    name:'',
                    router:'technology'
                  },
                  {
                    name:'',
                    router:'product'
                  },
                  {
                    name:'',
                    router:'semi-manufactures'
                  },
                  {
                    name:'',
                    router:'productProcess'
                  }
                ]
              },
              {
                name:'',
                router:'pc',
                chlid:[
                  {
                    name:'',
                    router:'internalOrder'
                  },{
                    name:'',
                    router:'approve'
                  },{
                    name:'',
                    router:'tasking'
                  },{
                    name:'',
                    router:'receive'
                  },
                  {
                    name:'',
                    router:'taskingRecord'
                  }
                ]
              },
              {
                name:'',
                router:'rwjg',
                chlid:[
                  {
                    name:'',
                    router:'jobAssignment'
                  },{
                    name:'',
                    router:'jobAssignmentHistory'
                  }
                ]
              },
              {
                name:'',
                router:'electronicHairMap'
              },
              {
                name:'',
                router:'customerManagement',
                chlid:[{
                    name:'',
                    router:'seeUpload'
                  },{
                    name:'',
                    router:'customerReview'
                  },{
                    name:'',
                    router:'customerProcessReview'
                  },{
                    name:'',
                    router:'customerUploadData'
                  }
                ]
              },
            ]
          }]
        }
      }
    },
    computed:{
        ...mapState(['menuList','ports'])
    },
    beforeMount(){
      this.$set(this.pageData.sidebarData[0],'chlid',JSON.parse(JSON.stringify(this.menuList)))
    }
  }

</script>
<style lang="less"></style>



