<template>
    <div class="taskingRecord">
        <search :data="searchData" :config="searchConfig" @search="handlerSearch"></search>
        <!-- <toolbar 
            :toolbarConfig="toolbarConfig"
            @setPrice="setPrice"
            
        ></toolbar> -->
        <my-table :data="tableData" :config="tableConfig" @pageChange="pageChange"   ></my-table>
    </div>
</template>
<script>
import {mapState} from 'vuex'
import rule from '../../assets/js/rule.js'
export default {
    data(){
        return{
            //
            userList:[],
            selectData:[],
            rules:{
                price:rule.require('number',''),
                doEmpNo:rule.require('string',''),
                planDate:rule.require('string',''),
                planHours:rule.require('number',''),
                actDate:rule.require('string',''),
            },
            searchData:{},
            searchConfig: [
                [
                    {
                        label: "",
                        field: "insideOrder",
                        type: "input",
                        placeholder: ""
                    },
                    {
                        label: "",
                        field: "prodName",
                        type: "input",
                        placeholder: ""
                    },
                    {
                        label: "",
                        field: "status",
                        type: "select",
                        placeholder: "",
                        childrens:[{name:'',value:'0'},{name:'',value:'1'},{name:'',value:'2'},{name:'',value:'3'}]
                    },
                ]
            ],
            tableData:[],
            tableConfig:{
                isHigh: false,
                isSelection: true,
                align: "center",
                isPage: true,
                pageNo: 1,
                pageSize: 20,
                total: 0,
                colConfig: [
                    {
                        fixed: false,
                        field: "insideOrder",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "prodName",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "crafworkName",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "price",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "doEmpName",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "amount",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "planDate",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "actDate",
                        width: "120",
                        isSort: false,
                        isResize: true,
                        label: "",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "getTime",
                        isSort: false,
                        isResize: true,
                        label: "",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "crafworkStandHours",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "planHours",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "statusName",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "",
                        align: "center",
                        type: "text"
                    }
                ]
            }
        }
    },
    computed:{
        ...mapState(['ports'])
    },
    methods:{
        getList(){
            this.$http.post(
                this.ports+'/taskReceiveEmpMes/list',
                {
                    pageSize:this.tableConfig.pageSize,
                    pageNo:this.tableConfig.pageNo,
                    ...this.searchData
                },
                data=>{
                    this.tableData=data.items;
                    this.tableConfig.total=data.numRows;
                },
                this
            )
        },
        handlerSearch(){
            this.tableConfig.pageNo=1
            this.tableConfig.pageSize=20
            this.getList()
        },
        setPrice(){
            this.$prompt('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                inputPattern: /^[1-9]\d*(\.\d+)?$/,
                inputErrorMessage: ''
            }).then(({ value }) => {
                var ids=[]
                for(let val of this.selectData){
                    ids.push(val.id)
                }
                this.$http.post(
                    this.ports+'/taskPondMes/setTaskPrice',
                    {taskPondIds:ids.join(','),price:value},
                    data=>{
                        this.getList()
                    },
                    this
                )
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });       
            });
        },
        // select(data){
        //     this.selectData=data
        //     if(data.length>=1){
        //         this.toolbarConfig.btnStatus=[false]
        //     }else {
        //         this.toolbarConfig.btnStatus=[true]
        //     }
        // },
        pageChange(){
            console.log('111')
            this.getList()
        }
    },
    mounted(){
        this.getList()
    }
}
</script>
<style lang="less">
    .taskingRecord{
       padding:20px;
       background:#fff;
        .my-list{
            padding:0 20px;
            .template{
                display: flex;
                .text{
                    flex: 1;
                }
                .operate{
                    width: 100px;
                    padding-left: 30px;
                    font-size: 14px;
                }
            }
        }
    }
</style>




